
.showcaseContainer {
    height: 80vh;
    background: url(../../assets/banner_1.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    object-fit: cover;
}

.showcaseOverlay {
    background: rgba(0, 0, 0, 0.8);
    width: 100%;
    height: 100%;
}
.showcaseContent {
    width: 100%;
    height: 100%;
    padding: 50px 100px;
    color: #f5f5f5;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    text-align: center;
}

.showcaseContent h2 {
    text-transform: uppercase;
    font-size: 42px;
    margin-bottom: 30px;
   
}
.showcaseContent p {
    font-size: 18px;
    margin-bottom: 10px;
}


@media(max-width: 800px) {
    .showcaseContainer {
        height: 90vh;
    }
    .showcaseContent {
        padding: 30px;
    }
}

